// Dark theme

$sidebar-dark-theme-color:                               $body-dark-theme-color !default;
$sidebar-dark-theme-bg:                                  $gray-dark-theme-400 !default;
$sidebar-dark-theme-borders:                             (all: 0) !default;
$sidebar-dark-theme-brand-bg:                            rgba($black, .2) !default;
$sidebar-dark-theme-header-bg:                           rgba($black, .2) !default;
$sidebar-dark-theme-form-border:                         0 !default;
$sidebar-dark-theme-form-bg:                             rgba($black, .1) !default;
$sidebar-dark-theme-form-color:                          $white !default;
$sidebar-dark-theme-form-placeholder-color:              rgba($white, .7) !default;
$sidebar-dark-theme-nav-title-color:                     rgba($white, .6) !default;
$sidebar-dark-theme-nav-link-color:                      rgba($white, .8) !default;
$sidebar-dark-theme-nav-link-bg:                         transparent !default;
$sidebar-dark-theme-nav-link-icon-color:                 $gray-600 !default;
$sidebar-dark-theme-nav-link-borders:                    0 !default;
$sidebar-dark-theme-nav-link-hover-color:                $white !default;
$sidebar-dark-theme-nav-link-hover-bg:                   desaturate($primary, 20%) !default;
$sidebar-dark-theme-nav-link-hover-icon-color:           $white !default;
$sidebar-dark-theme-nav-link-hover-borders:              0 !default;
$sidebar-dark-theme-nav-link-active-color:               $white !default;
$sidebar-dark-theme-nav-link-active-bg:                  rgba($white, .05) !default;
$sidebar-dark-theme-nav-link-active-icon-color:          $white !default;
$sidebar-dark-theme-nav-link-active-borders:             0 !default;
$sidebar-dark-theme-nav-link-disabled-color:             darken($white, 30%) !default;
$sidebar-dark-theme-nav-link-disabled-bg:                $sidebar-dark-theme-bg !default;
$sidebar-dark-theme-nav-link-disabled-icon-color:        $sidebar-dark-theme-nav-link-icon-color !default;
$sidebar-dark-theme-nav-link-disabled-borders:           0 !default;
$sidebar-dark-theme-nav-dropdown-color:                  $white !default;
$sidebar-dark-theme-nav-dropdown-bg:                   rgba(0, 0, 0, .2) !default;
$sidebar-dark-theme-nav-dropdown-borders:                0 !default;
$sidebar-dark-theme-nav-dropdown-indicator-color:        $gray-600 !default;
