$sidebar-dark-theme-nav-dropdown-indicator-hover-color:  $sidebar-dark-theme-nav-link-hover-color !default;
$sidebar-dark-theme-nav-dropdown-indicator-hover:        str-replace(url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 11 14'%3E%3Cpath fill='#{$sidebar-dark-theme-nav-dropdown-indicator-hover-color}' d='M9.148 2.352l-4.148 4.148 4.148 4.148q0.148 0.148 0.148 0.352t-0.148 0.352l-1.297 1.297q-0.148 0.148-0.352 0.148t-0.352-0.148l-5.797-5.797q-0.148-0.148-0.148-0.352t0.148-0.352l5.797-5.797q0.148-0.148 0.352-0.148t0.352 0.148l1.297 1.297q0.148 0.148 0.148 0.352t-0.148 0.352z'/%3E%3C/svg%3E"), "#", "%23") !default;
$sidebar-dark-theme-nav-tabs-border-color:               rgba($black, .2) !default;
$sidebar-dark-theme-nav-tabs-link-active-bg:             rgba($white, .05) !default;
$sidebar-dark-theme-nav-tabs-link-active-border-color:   rgba($black, .2) rgba($black, .2) desaturate($primary, 20%) !default;
$sidebar-dark-theme-nav-tabs-link-hover-bg:              rgba($black, .2) !default;
$sidebar-dark-theme-nav-tabs-link-hover-border-color:    rgba($black, .2) rgba($black, .2) $gray-200 !default;
$sidebar-dark-theme-tab-content-borders:                 ( top: 1px solid rgba($black, .2) ) !default;
$sidebar-dark-theme-footer-bg:                           rgba($black, .2) !default;
$sidebar-dark-theme-footer-borders:                      0 !default;
$sidebar-dark-theme-minimizer-borders:                   0 !default;
$sidebar-dark-theme-minimizer-bg:                        rgba($black, .2) !default;
$sidebar-dark-theme-minimizer-indicator-color:           $gray-600 !default;
$sidebar-dark-theme-minimizer-indicator:                 str-replace(url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 11 14'%3E%3Cpath fill='#{$sidebar-dark-theme-minimizer-indicator-color}' d='M9.148 2.352l-4.148 4.148 4.148 4.148q0.148 0.148 0.148 0.352t-0.148 0.352l-1.297 1.297q-0.148 0.148-0.352 0.148t-0.352-0.148l-5.797-5.797q-0.148-0.148-0.148-0.352t0.148-0.352l5.797-5.797q0.148-0.148 0.352-0.148t0.352 0.148l1.297 1.297q0.148 0.148 0.148 0.352t-0.148 0.352z'/%3E%3C/svg%3E"), "#", "%23") !default;
$sidebar-dark-theme-minimizer-hover-bg:                rgba(0, 0, 0, .3) !default;
$sidebar-dark-theme-minimizer-hover-indicator-color:     $sidebar-dark-theme-nav-link-hover-color !default;
$sidebar-dark-theme-minimizer-hover-indicator:           str-replace(url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 11 14'%3E%3Cpath fill='#{$sidebar-dark-theme-minimizer-hover-indicator-color}' d='M9.148 2.352l-4.148 4.148 4.148 4.148q0.148 0.148 0.148 0.352t-0.148 0.352l-1.297 1.297q-0.148 0.148-0.352 0.148t-0.352-0.148l-5.797-5.797q-0.148-0.148-0.148-0.352t0.148-0.352l5.797-5.797q0.148-0.148 0.352-0.148t0.352 0.148l1.297 1.297q0.148 0.148 0.148 0.352t-0.148 0.352z'/%3E%3C/svg%3E"), "#", "%23") !default;

$sidebar-dark-theme-variants-map: () !default;
// stylelint-disable-next-line scss/dollar-variable-default
$sidebar-dark-theme-variants-map: map-merge(
  (
    dark: (
      "sidebar-color":                               $sidebar-dark-theme-color,
      "sidebar-bg":                                  $sidebar-dark-theme-bg,
      "dark-theme-sidebar-bg":                       0,
      "sidebar-borders":                             $sidebar-dark-theme-borders,
      "dark-theme-sidebar-borders":                  0,
      "sidebar-brand-bg":                            $sidebar-dark-theme-brand-bg,
      "sidebar-header-bg":                           $sidebar-dark-theme-header-bg,
      "sidebar-form-border":                         $sidebar-dark-theme-form-border,
      "sidebar-form-bg":                             $sidebar-dark-theme-form-bg,
      "sidebar-form-color":                          $sidebar-dark-theme-form-color,
      "sidebar-form-placeholder-color":              $sidebar-dark-theme-form-placeholder-color,
      "sidebar-nav-title-color":                     $sidebar-dark-theme-nav-title-color,
      "sidebar-nav-link-color":                      $sidebar-dark-theme-nav-link-color,
      "sidebar-nav-link-bg":                         $sidebar-dark-theme-nav-link-bg,
      "sidebar-nav-link-icon-color":                 $sidebar-dark-theme-nav-link-icon-color,
      "sidebar-nav-link-borders":                    $sidebar-dark-theme-nav-link-borders,
      "sidebar-nav-link-hover-color":                $sidebar-dark-theme-nav-link-hover-color,
      "sidebar-nav-link-hover-bg":                   $sidebar-dark-theme-nav-link-hover-bg,
      "sidebar-nav-link-hover-icon-color":           $sidebar-dark-theme-nav-link-hover-icon-color,
      "sidebar-nav-link-hover-borders":              $sidebar-dark-theme-nav-link-hover-borders,
      "sidebar-nav-link-active-color":               $sidebar-dark-theme-nav-link-active-color,
      "sidebar-nav-link-active-bg":                  $sidebar-dark-theme-nav-link-active-bg,
      "sidebar-nav-link-active-icon-color":          $sidebar-dark-theme-nav-link-active-icon-color,
      "sidebar-nav-link-active-borders":             $sidebar-dark-theme-nav-link-active-borders,
      "sidebar-nav-link-disabled-color":             $sidebar-dark-theme-nav-link-disabled-color,
      "sidebar-nav-link-disabled-bg":                $sidebar-dark-theme-nav-link-disabled-bg,
      "sidebar-nav-link-disabled-icon-color":        $sidebar-dark-theme-nav-link-disabled-icon-color,
      "sidebar-nav-link-disabled-borders":           $sidebar-dark-theme-nav-link-disabled-borders,
      "sidebar-nav-dropdown-color":                  $sidebar-dark-theme-nav-dropdown-color,
      "sidebar-nav-dropdown-bg":                     $sidebar-dark-theme-nav-dropdown-bg,
      "sidebar-nav-dropdown-borders":                $sidebar-dark-theme-nav-dropdown-borders,
      "sidebar-nav-dropdown-indicator-color":        $sidebar-dark-theme-nav-dropdown-indicator-color,
      "sidebar-nav-dropdown-indicator":              $sidebar-dark-theme-nav-dropdown-indicator,
      "sidebar-nav-dropdown-indicator-hover-color":  $sidebar-dark-theme-nav-dropdown-indicator-hover-color,
      "sidebar-nav-dropdown-indicator-hover":        $sidebar-dark-theme-nav-dropdown-indicator-hover,
      "sidebar-nav-tabs-border-color":               $sidebar-dark-theme-nav-tabs-border-color,
      "sidebar-nav-tabs-link-active-bg":             $sidebar-dark-theme-nav-tabs-link-active-bg,
      "sidebar-nav-tabs-link-active-border-color":   $sidebar-dark-theme-nav-tabs-link-active-border-color,
      "sidebar-nav-tabs-link-hover-bg":              $sidebar-dark-theme-nav-tabs-link-hover-bg,
      "sidebar-nav-tabs-link-hover-border-color":    $sidebar-dark-theme-nav-tabs-link-hover-border-color,
      "sidebar-tab-content-borders":                 $sidebar-dark-theme-tab-content-borders,
      "sidebar-footer-bg":                           $sidebar-dark-theme-footer-bg,
      "sidebar-footer-borders":                      $sidebar-dark-theme-footer-borders,
      "sidebar-minimizer-borders":                   $sidebar-dark-theme-minimizer-borders,
      "sidebar-minimizer-bg":                        $sidebar-dark-theme-minimizer-bg,
      "sidebar-minimizer-indicator-color":           $sidebar-dark-theme-minimizer-indicator-color,
      "sidebar-minimizer-indicator":                 $sidebar-dark-theme-minimizer-indicator,
      "sidebar-minimizer-hover-bg":                  $sidebar-dark-theme-minimizer-hover-bg,
      "sidebar-minimizer-hover-indicator-color":     $sidebar-dark-theme-minimizer-hover-indicator-color,
      "sidebar-minimizer-hover-indicator":           $sidebar-dark-theme-minimizer-hover-indicator
    )
  ),
  $sidebar-dark-theme-variants-map
);

$sidebar-dark-theme-default-variant: "dark" !default;
$sidebar-dark-theme-variants: (
  map:      $sidebar-dark-theme-variants-map,
  default:  $sidebar-dark-theme-default-variant
) !default;

// stylelint-disable-next-line scss/dollar-variable-default
$sidebar-theme-map: map-merge(
  $sidebar-theme-map,
  (
    dark:  $sidebar-dark-theme-variants
  )
);
